'use client';

import { useState } from "react";
import { useRouter } from "next/navigation";
import ToggleChip, { ToggleValue } from "@/components/ui/ToggleChip";

/* ‚îÄ‚îÄ Filter options (edit here) ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
const PET_TYPES = [
  { id: "dog", label: "Dogs", icon: "üê∂" },
  { id: "cat", label: "Cats", icon: "üê±" },
] as const;

const GENDERS = ["Any", "Male", "Female"] as const;
const SIZES = ["Small", "Medium", "Large"] as const;
const AGES = ["Baby", "Young", "Adult", "Senior"] as const;

export default function PetSearchForm() {
  const router = useRouter();

  const [petType, setPetType] = useState<ToggleValue<typeof PET_TYPES>>(null);
  const [gender, setGender] = useState<ToggleValue<typeof GENDERS>>(null);
  const [size, setSize] = useState<ToggleValue<typeof SIZES>>(null);
  const [age, setAge] = useState<ToggleValue<typeof AGES>>(null);

  const handleSubmit = () => {
    const params = new URLSearchParams();
    if (petType) params.set("type", petType);
    if (gender && gender !== "Any") params.set("gender", gender);
    if (size) params.set("size", size);
    if (age) params.set("age", age);

    router.push(`/search?${params.toString()}`);
  };

  return (
    <div className="p-6 space-y-8">
      <h1 className="text-2xl font-semibold">Pet Search</h1>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">Pet Types</h2>
        <ToggleChip
          items={PET_TYPES}
          selected={petType}
          onChange={setPetType}
          getId={(p) => p.id}
          render={(p) => (
            <span className="flex items-center gap-1">
              {p.icon}
              <span>{p.label}</span>
            </span>
          )}
        />
      </section>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Gender <span className="text-sm text-gray-400">(Optional)</span>
        </h2>
        <ToggleChip
          items={GENDERS}
          selected={gender}
          onChange={setGender}
          render={(g) => g}
        />
      </section>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Size <span className="text-sm text-gray-400">(Optional)</span>
        </h2>
        <ToggleChip
          items={SIZES}
          selected={size}
          onChange={setSize}
          render={(s) => s}
        />
      </section>

      <section className="space-y-3">
        <h2 className="text-lg font-medium">
          Age <span className="text-sm text-gray-400">(Optional)</span>
        </h2>
        <ToggleChip
          items={AGES}
          selected={age}
          onChange={setAge}
          render={(a) => a}
        />
      </section>

      <div className="pt-4">
        <button
          onClick={handleSubmit}
          className="w-full rounded-full bg-orange-500 py-3 text-center text-white font-medium shadow hover:bg-orange-600"
        >
          Search
        </button>
      </div>
    </div>
  );
}
